import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { RegistroPermisoPage } from './registro-permiso.page';

const routes: Routes = [
  {
    path: '',
    component: RegistroPermisoPage,
  },
  {
    path: 'crear-permiso',
    loadChildren: () => import('src/app/pages/SST/crear-permiso/crear-permiso.module').then(m => m.CrearPermisoPageModule),
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class RegistroPermisoPageRoutingModule {}
