import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController, Platform } from '@ionic/angular';
import { Observable, from } from 'rxjs';
import { TipoPermiso } from 'src/app/interfaces/tipo-permiso';
import { TrabajadorMigrado } from 'src/app/interfaces/trabajador-migrado';
import { DataBaseService } from 'src/app/services/SST/data-base.service';
import { DataService } from 'src/app/services/SST/data.service';
import { NetworkService } from 'src/app/services/network.service';
import { UtilsService } from 'src/app/services/utils.service';

@Component({
  selector: 'app-registro-permiso',
  templateUrl: './registro-permiso.page.html',
  styleUrls: ['./registro-permiso.page.scss'],
})
export class RegistroPermisoPage implements OnInit {
  fechaMigracion: String;
  tipoDeMigrar: Boolean;
  listaTipoPermiso: TipoPermiso[] = [];
  tipoPermiso: string;

  constructor(
    private dataService: DataService,
    private utils: UtilsService,
    private router: Router,
    private alertController: AlertController,
    private dataBaseService: DataBaseService,
    public platform: Platform,
    private chekInternet: NetworkService
  ) {}

  ngOnInit() {
    // this.getFechaMigracion();
    this.tiposDePermiso();
  }

  onChangePermiso(event: any) {
    this.tipoPermiso = event.detail.value;
  }

  async getData(): Promise<void> {
    await this.utils.presentLoading();
    let internet = await this.chekInternet.testNetworkConnection();
    if (!this.tipoPermiso) {
      const alert = await this.alertController.create({
        header: 'Alerta',
        message: 'Debe seleccionar un tipo de permiso',
        buttons: ['OK'],
      });
      await alert.present();
      this.utils.dismissLoading();
      return;
    }

    try {
      if (internet) {
        await this.dataService.infoTrabajo(this.tipoPermiso);
        await this.dataService.infoPermisoTrabajo();
        this.navegarToCrearPermiso();
      } else {
        this.navegarToCrearPermiso();
      }
    } catch (error) {
      console.log(error);
      let alert = this.alertController.create({
        header: 'Error',
        message: 'Se ha presentado un error al sincronizar la información',
        buttons: ['OK'],
      });
      (await alert).present();
    } finally {
      this.utils.dismissLoading();
    }
  }

  getFechaMigracion(): String {
    const lastSync = localStorage.getItem('lastSync');
    if (!lastSync) {
      this.fechaMigracion = 'No se ha realizado la migración';
      this.tipoDeMigrar = false;
      return this.fechaMigracion;
    }
    const date = new Date(lastSync);
    const currentDate = new Date();
    const daysDifference = this.utils.getDaysDifference(date, currentDate);
    const optionsDate: Intl.DateTimeFormatOptions = { month: 'short', day: '2-digit', year: 'numeric' };
    const optionsTime: Intl.DateTimeFormatOptions = { hour: '2-digit', minute: '2-digit', hour12: false };

    const formattedDate = date.toLocaleDateString('en-US', optionsDate);
    const formattedTime = date.toLocaleTimeString('en-US', optionsTime);
    const fechaMigracion = `${formattedDate} - ${formattedTime}`;
    this.fechaMigracion = fechaMigracion;
    this.tipoDeMigrar = true;
    return this.fechaMigracion;
  }

  async tiposDePermiso() {
    try {
      let internet = await this.chekInternet.testNetworkConnection();
      if (internet) {
        await this.dataService.infoPermisoTrabajo();
      }
      this.dataBaseService.open().then(() => {
        this.dataBaseService.getAllTipoPermiso().subscribe({
          next: (listaTipoPermiso: TipoPermiso[]) => {
            this.listaTipoPermiso = listaTipoPermiso;
          },
          error: error => {
            console.error('Error al obtener los tipos de permiso:', error);
          },
        });
      });
    } catch (er) {
      this.dataBaseService.close();
    }
  }

  async navegarToCrearPermiso(): Promise<void> {
    let strTipoPermiso = this.listaTipoPermiso.filter(
      tipoPermiso => tipoPermiso.PK_ID_PermisoAlturas_MaestroTipoPermiso.toString() === this.tipoPermiso
    );
    this.router.navigate(['/home/permisos/registro-permiso/crear-permiso'], { state: { tipo: strTipoPermiso[0].strDescripcion } });
  }

  async sysConnectedCheked(): Promise<{ connected: boolean; platform: string }> {
    let platform = this.platform.platforms()[0];
    let conection = await this.chekInternet.testNetworkConnection();
    if (conection) {
      return { connected: true, platform };
    }
    return { connected: false, platform };
  }

  back() {
    this.router.navigate(['/home/permisos']);
  }
}
